.Input {
    width: 90%;
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}

.Label {
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement {
    outline: none;
    border: 1px solid #ccc;
    background-color: white;
    font: inherit;
    padding: 6px 10px;
    display: block;
    width: 100%;
}

.InputElement:focus {
    background-color: #ccc;
    outline: none;
}

.Invalid {
    border: 1px solid red;
    background-color: rgb(245, 156, 146);
}



